import React from 'react';
import './Table.css'

const Table = ({ data, click }) => {
    function ballas(e) {
        const arr = []
        for (let i = 1; i <= e; i++) {
            arr.push(i);
        }
        return arr.map(e => <img class="img" src="star.png" alt="" />)
    }
    return (
        <div>

            <table class="table-fill">
                <thead>
                    <tr>
                        <th class="text-left">Ismi</th>
                        <th class="text-left">Familiyasi</th>
                        <th class="text-left">Stars</th>
                        <th class="text-left">Ballar soni</th>
                    </tr>
                </thead>

                <tbody class="table-hover">
                    {
                        data.map(elem => (
                            <tr onClick={() => {
                                click(elem)
                            }}>
                                <td class="text-left">{elem.ismi}</td>
                                <td class="text-left">{elem.familiyasi}</td>
                                <td class="text-left">
                                    {
                                        elem.balls >= 5 ? <div>
                                            <img class="img" src="star.png" alt="" />
                                            <img class="img" src="star.png" alt="" />
                                            <img class="img" src="star.png" alt="" />
                                            <img class="img" src="star.png" alt="" />
                                            <img class="img" src="star.png" alt="" />
                                        </div> :
                                            ballas(elem.balls)
                                    }
                                </td>
                                <td class="text-left">{elem.balls}</td>
                            </tr>
                        )
                        )

                    }

                </tbody>
            </table>

        </div >
    );
}

export default Table;
